export const EVENT_SLOT_CONTENT_CHANGE = 'slot-content-change';
export const EVENT_AFTER_SLOT_CONTENT_CHANGE = 'after-slot-content-change';

export const EVENT_DIALOG_CLOSED = 'onDialogClosed';

const listeners: { [key: string]: EventListener[] } = {};

export type EventListener = (params?: any) => void;

export interface EventBus {
  register: (event: string, callback: EventListener) => void;
  unregister: (event: string, callback: EventListener) => void;
  dispatch: (event: string, params?: any) => void;
  hasListeners: (event: string) => boolean;
}

export const createEventBus = (): EventBus => ({
  register: (event: string, callback: EventListener): void => {
    if (!listeners[event]) {
      listeners[event] = [];
    }
    if (!listeners[event].includes(callback)) {
      listeners[event].push(callback);
    }
  },

  unregister: (event: string, callback: EventListener): void => {
    listeners[event] = listeners[event].filter((l) => l !== callback);
  },

  dispatch: (event: string, params?: any): void => {
    if (!listeners[event]) {
      return;
    }
    listeners[event].map((listener: EventListener) => {
      listener(params);
    });
  },

  hasListeners: (event: string): boolean => {
    return listeners[event] && listeners[event].length > 0;
  },
});
